App {
    text-align: center;
    margin-left: 400px;
    margin-top: 50px;
    border: 5px solid #e10f0f;
    padding: 20px;
    border-radius: 4px;
    width: 700px;
    border-style: inset double;
    background-color: ghostwhite;
  }
  
  
  h1 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
    margin-bottom: 20px;
  }
  
  li {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 10px;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  